.mainBody {
  display: grid;
  grid-template-columns: 20% 80%;
}
.navbartext {
  padding: 7px 0 0 40px;
  margin: 0px;
}
.NavbarButton {
  outline: none;
  border: none;
  background-color: #e83535;
  padding: 15px;
  margin-left: 38px;
  border-radius: 30px;
  width: 120px;
  display: flex;
  justify-content: center;
  text-align: center;
  color: white;
  font-size: 1em;
}
.mainheading {
  padding-left: 40px;
}
.rightContainertext {
  font-size: 4em;
  padding: 0px !important;
  margin-top: 0;
}
.rightContainerUpper {
  display: grid;
  grid-template-columns: 60% 30%;
}
.card {
  box-shadow: -2px 2px 26px -5px rgb(0 0 0 / 75%);
  -webkit-box-shadow: -2px 2px 26px -5px rgb(0 0 0 / 75%);
  -moz-box-shadow: -2px 2px 26px -5px rgba(0, 0, 0, 0.75);
  /* width: 30%; */
  height: fit-content;
  display: flex;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  margin-right: 30px;
  padding: 10px 10px 0 10px;
  flex: 0 0 auto;
  width: 16%;
}
.innercard {
  max-width: 100%;
  height: auto;
}
.box {
  display: flex;
  flex-direction: row;
  max-width: 99%;
  overflow-x: scroll;
  padding: 15px;
}
@media only screen and (max-width: 768px) {
  .mainBody {
    display: unset;
  }
  .card {
    border-radius: 10px;
    flex: 0 0 auto;
    width: 43%;
    padding: 0;
    margin: auto auto;
    margin-bottom: 13px;
    padding: 5px;
    justify-content: space-around;
    box-shadow: -1px 1px 5px 0px rgba(0, 0, 0, 0.66);
    -webkit-box-shadow: -1px 1px 5px 0px rgba(0, 0, 0, 0.66);
    -moz-box-shadow: -1px 1px 5px 0px rgba(0, 0, 0, 0.66);
  }
  .rightContainerUpper {
    display: unset;
  }
  .box {
    flex-wrap: wrap;
    flex-shrink: 0;
    max-width: 100%;
    overflow: unset;
  }
  .rightContainertext {
    font-size: 2.5em;
  }
}
